.intro {
  color: $color-alice-blue;
  background-color: $color-black-pearl;
  background-image: url("../../img/main-bg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @include vp-1023 {
    background-image: url("../../img/main-bg-tablet.jpg");
  }

  @include vp-767 {
    background-image: url("../../img/main-bg-mobile.jpg");
  }

  @include retina {

    @include vp-1023 {
      background-image: url("../../img/main-bg-tablet@2x.jpg");
    }

    // @include vp-1919 {
    //   background-image: url("../../img/main-bg@2x.jpg");
    // }

    @include vp-767 {
      background-image: url("../../img/main-bg-mobile@2x.jpg");
    }

    background-image: url("../../img/main-bg@2x.jpg");
  }
}

.intro__container {
  min-height: 880px;
  padding: 305px 125px 150px;

  @include vp-1023 {
    min-height: 640px;
    padding: 180px 0 150px;
  }

  @include vp-767 {
    min-height: 560px;
    padding-top: 150px;
  }
}

.intro__title {
  margin: 0 0 32px 0;
  font-family: $ff-roboto;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  text-transform: uppercase;
  letter-spacing: 0.07em;

  @include vp-1023 {
    margin-bottom: 0;
    font-size: 64px;
    line-height: 75px;
  }

  @include vp-767 {
    margin-bottom: 5px;
    font-size: 36px;
    line-height: 43px;
  }
}

.intro__description {
  max-width: 685px;
  margin: 0px 0px 47px 0px;
  font-size: 22px;
  line-height: 34px;

  @include vp-1023 {
    margin-bottom: 58px;
  }

  @include vp-767 {
    max-width: 300px;
    font-size: 14px;
    line-height: 22px;
  }
}

.intro__button {
  margin-bottom: 40px;
  font-size: 18px;
  line-height: 21px;
  color: $color-alice-blue;
  background-color: $color-deep-sky-blue;
  border-color: $color-deep-sky-blue;

  @include vp-1023 {
    margin-left: 45px;
  }

  @include vp-767 {
    margin-left: 0;
    font-size: 14px;
    line-height: 16px;
  }

  @include hover-focus {
    background-color: $color-black-pearl;
    border-color: $color-alice-blue;
  }
}
